import React, { useState } from "react";

export default function About(props) {
  const [mystyle, setmystyle] = useState({
    color: "black",
    backgroundColor: "white",
  });
  const [btntext, setbtntext] = useState("Enable Dark Mode");
  const Toggle = () => {
    if (mystyle.color === "black") {
      setmystyle({
        color: "white",
        backgroundColor: "black",
      });
      setbtntext("Enable Light Mode");
    } else {
      setmystyle({
        color: "black",
        backgroundColor: "white",
      });
      setbtntext("Enable Dark Mode");
    }
  };
  return (
    <>
      <h2>{props.text}</h2>
      <div className="container my-3" style={mystyle}>
        <div class="accordion" id="accordionPanelsStayOpenExample">
          <div class="accordion-item">
            <h2 class="accordion-header" id="panelsStayOpen-headingOne">
              <button
                class="accordion-button"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#panelsStayOpen-collapseOne"
                aria-expanded="true"
                aria-controls="panelsStayOpen-collapseOne"
                style={mystyle}
              >
               <strong>ANALYSE YOUR TEXT</strong>
              </button>
            </h2>
            <div
              id="panelsStayOpen-collapseOne"
              class="accordion-collapse collapse show"
              aria-labelledby="panelsStayOpen-headingOne"
            >
              <div class="accordion-body" style={mystyle}>
                <strong>
                  Text analysis is really the process of distilling information
                  and meaning from text. For example, this can be analyzing text
                  written in reviews by customers on a retailer's website or
                  analysing documentation to understand its purpose.
                </strong>
              </div>
            </div>
          </div>
          <div class="accordion-item">
            <h2 class="accordion-header" id="panelsStayOpen-headingTwo">
              <button
                class="accordion-button collapsed"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#panelsStayOpen-collapseTwo"
                aria-expanded="false"
                aria-controls="panelsStayOpen-collapseTwo"
                style={mystyle}
              >
                <strong>FREE TO USE</strong>
              </button>
            </h2>
            <div
              id="panelsStayOpen-collapseTwo"
              class="accordion-collapse collapse"
              aria-labelledby="panelsStayOpen-headingTwo"
              style={mystyle}
            >
              <div class="accordion-body" style={mystyle}>
                <strong>
                  Free Use means a specific case in which people can use the
                  work without permission according to the copyright limitation
                  on the work. In this case, the user should comply with the
                  specific legal conditions.
                </strong>
              </div>
            </div>
          </div>
          <div class="accordion-item">
            <h2 class="accordion-header" id="panelsStayOpen-headingThree">
              <button
                class="accordion-button collapsed"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#panelsStayOpen-collapseThree"
                aria-expanded="false"
                aria-controls="panelsStayOpen-collapseThree"
                style={mystyle}
              >
                <strong>BROWSER FREINDLY</strong>
              </button>
            </h2>
            <div
              id="panelsStayOpen-collapseThree"
              class="accordion-collapse collapse"
              aria-labelledby="panelsStayOpen-headingThree"
            >
              <div class="accordion-body" style={mystyle}>
                <strong>
                  Friendly is safe for teens. It's designed to be a one
                  stop-shop for access to accounts on different social media
                  platforms that the user already has established, thus security
                  preferences should already be established within each
                  platform.
                </strong>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="btn  my-3">
        <button onClick={Toggle} className="btntext btn btn-info">
          {btntext}
        </button>
      </div>
    </>
  );
}
