import React, { useState } from "react";
import copy from "copy-to-clipboard";

export default function Form(props) {
  const handleupclick = () => {
    // alert("changed to uppercase sucessfully ");
    let newtext = text.toUpperCase();
    setText(newtext);
    props.showAlert("Converted To Uppercase", "success");
  };
  const handleitclick = () => {
    // alert("changed to lowercase Sucessfully");
    let it = text.toLowerCase();
    setText(it);
    props.showAlert("Converted To Lowercase","Success")
  };
  const handleclearclick = () => {
    //   alert("Text Cleared Successfully");
    let it = "";
    setText(it);
    props.showAlert("Text Cleared","Success")
    
  };
  const copyToClipboard = () => {
    copy(text);
    alert(`You have copied "${text}"`);
  };
  const pre=()=>
  {
    alert(`"${text}"`)
    props.showAlert("Preview Enabled","Success")
  }
  const removespace=()=>
  {
    let newtext=text.split(/[ ]+/)
    setText(newtext.join(" "))
    props.showAlert("Space Removed","Success")
  }
  const handleonchange = (event) => {
    console.log("Onchange clicked");
    setText(event.target.value);
  };
  const [text, setText] = useState("");
  return (
    <>
      <div className="con my-1">
        <h2 className="my-4" >{props.heading}</h2>
        <div className="">
          <label htmlFor="myBox">{props.lab}</label>
          <textarea
            className="form-control"
            id="myBox"
            value={text}
            rows="8"
            onChange={handleonchange}
          ></textarea>
        </div>
        <button disabled={text.length===0}className="btn btn-primary mx-2 my-2" onClick={handleupclick}>
          Convert To Uppercase
        </button>
        <button disabled={text.length===0} className="btn btn-danger mx-2 my-2 " onClick={handleitclick}>
          Convert To Lowercase
        </button>
        <button disabled={text.length===0} className="btn btn-success mx-2 my-2" onClick={handleclearclick}>
          Clear text
        </button>
        <button disabled={text.length===0} className="btn btn-info mx-2 my-2" onClick={copyToClipboard}>
          Copy to Clipboard
        </button>
        <button disabled={text.length===0} className="btn btn-warning mx-2 my-2" onClick={pre}>PREVIEW</button>
        <button disabled={text.length===0} className="btn btn-secondary mx-2 my-2"onClick={removespace}>Remove Extra Space</button>
      </div>
      <div className="container1 ">
        <h2>TEXT SUMMARY</h2>
        <p>Total Words-{text.split(" ").filter((element)=>{return element.length!==0}).length}</p>
        <p>Total Characters-{text.length}</p>
        <p>
          Total Minutes To read the Contents-{0.008 * text.split(" ").filter((element)=>{return element.length!==0}).length}
        </p>
        <p>Total Time to Speak a word-{0.003375*text.split(" ").filter((element)=>{return element.length!==0}).length}</p>
      </div>
      <hr className="dropdown-divider btn-danger" />
      <div className="container2">
        <h3>Preview Of the Content</h3>
        <p>{text.length>0?text:'Nothing To Preview'}</p>
      </div>
      <hr className="dropdown-divider btn-danger" />
    </>
  );
}
