import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom';

export default function Navbar(props) {
    return (
        <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
            <div className="container-fluid">
                <Link className="navbar-brand" to="/">{props.title}</Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link className="nav-link" aria-current="page" to="/"><strong>Home</strong></Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/about"><strong>{props.aboutText}</strong></Link>
                        </li>
                    </ul>
                    <div className="bg-primary d-flex rounded mx-2" onClick={()=>{props.toggleMode('primary')}} style={{height:'20px',width:'20px',cursor:'pointer'}}/>
                    <div className="bg-warning d-flex rounded mx-2" onClick={()=>{props.toggleMode('warning')}} style={{height:'20px',width:'20px',cursor:'pointer'}}/>
                    <div className="bg-danger d-flex rounded mx-2" onClick={()=>{props.toggleMode('danger')}} style={{height:'20px',width:'20px',cursor:'pointer'}}/>
                    <div className="bg-info d-flex rounded mx-2" onClick={()=>{props.toggleMode('info')}} style={{height:'20px',width:'20px',cursor:'pointer'}}/>
                    <div className="bg-secondary d-flex rounded mx-2" onClick={()=>{props.toggleMode('secondary')}} style={{height:'20px',width:'20px',cursor:'pointer'}}/>
                    <div className="bg-light d-flex rounded mx-2" onClick={()=>{props.toggleMode('light')}} style={{height:'20px',width:'20px',cursor:'pointer'}}/>
                    <div className="bg-dark d-flex rounded mx-2" onClick={()=>{props.toggleMode('dark')}} style={{height:'20px',width:'20px',cursor:'pointer'}}/>
                    
                    {/* <div className={`form-check form-switch text-${props.mode==='light'?'dark':'light'}`}>
                        <input className="form-check-input" onClick={()=>{props.toggleMode(null)}} type="checkbox" id="flexSwitchCheckDefault"/>
                        <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Enable DarkMode</label>
                    </div> */}
                </div>
            </div>
        </nav>
    )
}

Navbar.propTypes = {
    title: PropTypes.string.isRequired,
    aboutText: PropTypes.string.isRequired
}

Navbar.defaultProps = {
    title: 'Set title here',
    aboutText: 'About'
  };